import { Component } from '@angular/core';
import { IonicPage, Events, NavParams, NavController,AlertController, LoadingController, ToastController } from 'ionic-angular';
import { UserProvider } from '../../providers/user/user';
 
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';

@IonicPage({
    name: 'page-sales-create',
    segment: 'sales-create'
})

@Component({
    selector: 'page-sales-create',
    templateUrl: 'sales-create.html'
})

export class SalesCreatePage {
    Asset: any;
    Amount: any;
    Price: any;
    MinQty: any;
    MinPrice: any;
    disable: boolean = true;
    public salesForm: FormGroup;
    item: any;
    Currency: any;

    name: any;
    tooltipEvent: 'click' | 'press' = 'click';


    constructor(public navCtrl: NavController,public alertCtrl: AlertController, public translate: TranslateService, private _fb: FormBuilder, public params: NavParams, public events: Events, public user: UserProvider, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {
        this.translate.setDefaultLang('en');
        this.translate.use('es');

        this.item = this.params.get('item');
        console.log(this.item);

        this.translate.get('sales-create-page').subscribe((translated: string) => {
            // console.log(translated);
            // //=> 'Hello world'

            // You can call instant() here
            var item1_para_tooltip = this.translate.instant('sales-create-page.form.tooltip.item1_para_tooltip');

            this.name = item1_para_tooltip + this.item.SPVNAME;

        })
        this.Currency = this.item.Currency;
        this.Price = this.item.share_value;
        console.log(this.Currency, this.Price);
        this.salesForm = this._fb.group({
            Amount: ['', [Validators.required]],
            Price: ['', [Validators.required]],
            MinPrice: ['', [Validators.required]],
            MinQty: ['', [Validators.required]],

        });


    }

    sendData() {

        var fa = JSON.parse(localStorage.getItem('2fa'));


        if(fa === 0){
    
          this.translate.get('activate-tfa').subscribe((translated: string) => {
            // console.log(translated);
            // //=> 'Hello world'
          
            // You can call instant() here
            var title = this.translate.instant('activate-tfa.alert.title');
            var message = this.translate.instant('activate-tfa.alert.message');
            var activate_2FA_btn = this.translate.instant('activate-tfa.alert.activate_2FA_btn');
            var continue_without_2FA_btn = this.translate.instant('activate-tfa.alert.continue_without_2FA_btn');
    
          const alert = this.alertCtrl.create({
            title: title,
            message: message,
            buttons: [
              {
                text: activate_2FA_btn,
                role: 'yes',
                handler: () => {
                 this.navCtrl.push('page-my-security');
                }
              },
              {
                text: continue_without_2FA_btn,
                handler: () => {
    
                  this.confrimTransction();
                 
                 
                }
              }
            ]
          });
          alert.present();
    
        })
    
        }else{
    
          this.confrimTransction();
    
         
      }



    }

    confrimTransction(){

        this.translate.get('sales-create-page').subscribe((translated: string) => {
            // console.log(translated);
            // //=> 'Hello world'

            // You can call instant() here
            var loader = this.translate.instant('sales-create-page.form.toast.loader');
            var message_success_toast = this.translate.instant('sales-create-page.form.toast.message_success_toast');

            let loading = this.loadingCtrl.create({
                content: loader
            });
            loading.present();
            this.user.createSellOffer(this.Currency, this.salesForm.value.Amount, this.salesForm.value.Price, this.salesForm.value.MinPrice, this.salesForm.value.MinQty).subscribe(
                data => {


                    let datas = JSON.parse(JSON.stringify(data));
                    console.log(datas);

                    if (datas.success) {

                        let toast = this.toastCtrl.create({
                            message: message_success_toast,
                            duration: 3000,
                            position: 'top'
                        });
                        toast.present();

                        toast.onDidDismiss(() => {

                            this.navCtrl.setRoot('page-investment-portfolio');

                        });

                    }


                },
                error => {
                    console.log(error.JSON);
                    setTimeout(() => {
                        loading.dismiss();
                        this.navCtrl.setRoot('page-general_error');
                    }, 1000);
                },
                () => {


                    loading.dismiss();


                }
            )
        })
    }


}



